<template lang="html">
  <div class="token__resp">
    <div class="token__resp--header">
      <div class="token__resp--left">
        <h2>Token History</h2>
        <!-- <div class="">
        </div> -->
        <span @click="toggleFilter">Filter By Date <v-icon size="10" color="#49C5FE">fa-chevron-down</v-icon></span>
      </div>
      <div class="token__resp--right">
        <h2>Token Balances:</h2>
        <h1>{{ userToken.token_amount }}</h1>
      </div>
    </div>
    <div class="token__resp--filter" v-if="handleFilter">
      <div class="datepicker--border-modal">
        <v-menu
          ref="menu"
          v-model="menu"
          :close-on-content-click="false"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width="290px"
        >
          <v-text-field
            slot="activator"
            label="From"
            solo
            color="black"
            v-model="start_time"
            append-icon="event"
            @click="togleDatepicker"
          ></v-text-field>
          <v-date-picker
            v-model="start_time"
            v-if="menu"
            no-title
          >
            <v-spacer></v-spacer>
            <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
            <v-btn flat color="primary" @click="menu = false">OK</v-btn>
          </v-date-picker>
        </v-menu>
      </div>
      <div class="datepicker--border-modal">
        <v-menu
          ref="menu"
          v-model="menu2"
          :close-on-content-click="false"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width="290px"
        >
          <v-text-field
            slot="activator"
            label="To"
            solo
            color="black"
            v-model="end_time"
            append-icon="event"
            @click="togleDatepicker2"
          ></v-text-field>
          <v-date-picker
            v-model="end_time"
            v-if="menu2"
            no-title
            :min="start_time"
          >
            <v-spacer></v-spacer>
            <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
            <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
          </v-date-picker>
        </v-menu>
      </div>
      <div class="trans__btn--quarterround filter--btn" @click="filterDate">Apply</div>
      <div class="trans__btn--quarterround filter--btn" @click="resetFilter">Reset</div>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'userToken'
  ],
  data: () => ({
    menu: false,
    menu2: false,
    start_time: null,
    end_time: null,
    handleFilter: false
  }),
  computed: {

  },
  methods: {
    toggleFilter () {
      this.handleFilter = !this.handleFilter
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    filterDate () {
      const rangeDate = {
        start_time: this.start_time,
        end_time: this.end_time
      }

      this.$store.dispatch("POST_TOKEN_FILTER_DATE", rangeDate)
    },
    resetFilter () {
      this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
      this.start_time = null
      this.end_time = null
    },
    reqToken () {
      const tokenAmount = {
        token_amount: this.tokenAmount
      }
      this.$store.dispatch('POST_COIN', tokenAmount);
      // console.log(tokenAmount);
      this.$store.commit('toggleDialog');
    }
  }
}
</script>

<style lang="scss">
  .datepicker--border-modal {
    width: 35%;

    .v-menu {
      width: 100%;
    }
    .v-text-field.v-text-field--solo .v-input__control {
      height: 34px !important;
      min-height: 34px !important;
    }
    .theme--light.v-text-field--solo .v-input__slot {
      border-radius: 5px;
      box-shadow: none;
      background: transparent;
    }
    .theme--light.v-label {
      color: $white;
      font-size: .9em;
    }
    .theme--light.v-input:not(.v-input--is-disabled) input, .theme--light.v-input:not(.v-input--is-disabled) textarea {
      color: $white;
      font-size: .9em;
    }
    .theme--light.v-icon {
      color: $white;
    }
  }
</style>



